import { useFoodStateValue } from '../context/FoodStateContext';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers';
import { v4 as uuidv4 } from 'uuid';

//! pass as a food object => then destrucure
export const useManageFoodState = (
  title,
  imgSrc,
  nutrition,
  foodId,
  grammageInfo,
  price,
  variants,
  category,
  itemId
) => {
  const { foodState, setFoodState } = useFoodStateValue();
  const { isFoodAvailable } = useFoodStateHelpers();

  const addFoodToState = () => {
    if (!isFoodAvailable(title)) {
      console.log(grammageInfo);
      setFoodState([
        ...foodState,
        {
          foodId,
          itemId: uuidv4(),
          category,
          title,
          variants: variants || [],
          nutrition,
          imgSrc,
          grammage: {
            value: 0,
            valueInMainUnit: 0,
            valueInDefaultUnit: 0,
            unit: grammageInfo.defaultUnit,
          },
          grammageInfo,
          price,
        },
      ]);
      console.log(
        `%c No ${title} copy, I have added it!`,
        'font-size: 20px;font-weight: bold;color:red;'
      );
    } else {
      console.log(`${title} is already included!`);
    }
  };

  const removeFoodFromState = () => {
    if (isFoodAvailable(title)) {
      console.log();
      const newFoodState = foodState.filter((food) => food.title !== title);
      setFoodState(newFoodState);
      console.log(
        `%c ${title} is included, I have removed it!`,
        'font-size: 20px;font-weight: bold;color:red;'
      );
    } else {
      console.log(`${title} is not included!`);
    }
  };

  const removeItemFromState = (title, itemId) => {
    console.log(title, itemId);
    const newFoodState = foodState.filter((food) => food.itemId !== itemId);
    setFoodState(newFoodState);
    // console.log('foodState', foodState);
    // console.log('newFoodState', newFoodState);
  };

  return {
    addFoodToState,
    removeFoodFromState,
    removeItemFromState,
  };
};
