import React, { useState } from 'react';

import { makeStyles } from '@material-ui/core/styles';

import Chip from '@material-ui/core/Chip';
import { ProsCons } from './ProsCons';
import { Summary } from './Summary';

const useStyles = makeStyles((theme) => ({
  margin: {
    margin: '12px 12px 0px 12px',
  },
}));

export const NutritionInfo = ({ info, nutrition, name, nutritionType }) => {
  const [activePage, setActivePage] = useState(0);

  const classes = useStyles();

  return (
    <>
      <div className='nutrition-info'>
        <div
          className='nutrition-info__head'
          style={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'center',
          }}
        >
          <Chip
            className={classes.margin}
            label={'Shrnutí'}
            clickable
            variant={activePage === 0 ? undefined : 'outlined'}
            onClick={() => setActivePage(0)}
            color='primary'
          />
          <Chip
            className={classes.margin}
            label={'Shrnutí - 100g'}
            clickable
            variant={activePage === 1 ? undefined : 'outlined'}
            onClick={() => setActivePage(1)}
            color='primary'
          />
          <Chip
            className={classes.margin}
            label={'Pros & Cons'}
            clickable
            variant={activePage === 2 ? undefined : 'outlined'}
            onClick={() => setActivePage(2)}
            color='primary'
          />

          {activePage === 0 ? (
            <Summary name={name} nutritionType={nutritionType} />
          ) : activePage === 1 ? (
            <div>Shrnutí</div>
          ) : activePage === 2 ? (
            <ProsCons info={info} nutrition={nutrition} />
          ) : (
            ''
          )}
        </div>
      </div>
    </>
  );
};
